.App {
  text-align: center;
}
.name{
  color:blue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.color{
  display: block;
  height: 38px;
  /* margin: 6px 12px; */
  margin: 10px 10px 25px;
  width: 75px;
  border: none;
  overflow: hidden;
  /* display: inline-flex; */
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
